import React, { useState } from "react";
import { Container, Row, Col, Form, Button, InputGroup } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faUser,
  faLock,
  faCommentDots,
} from "@fortawesome/free-solid-svg-icons";
import "../css/Login.css"; // CSS 파일의 실제 경로로 확인하세요.
import Navs from "../components/Nav";

function Login() {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await fetch("/api/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ username, password }),
      });
      if (response.ok) {
        // 로그인 성공
        // 예: 리다이렉트 or 다른 작업 수행
        console.log("로그인 성공");
      } else {
        // 로그인 실패
        // 예: 에러 메시지 표시
        console.log("로그인 실패 돌아가");
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };
  return (
    <div>
      <Navs />
      {/* 생략된 내용: Nav 컴포넌트가 있다면 여기에 포함시키세요. */}
      <div id="content" className="my-custom-content">
        <Container>
          <Row className="justify-content-md-center">
            <Col md={6} className="login-form-container">
              <div className="text-center mb-4">
                {/* 로고 이미지 */}
                <img
                  src="/img/r1.png"
                  alt="Read Fit 로고"
                  className="login-logo"
                />
              </div>

              <Form onSubmit={handleLogin}>
                <InputGroup className="mb-3 w-75">
                  <InputGroup.Text>
                    <FontAwesomeIcon icon={faUser} />
                  </InputGroup.Text>
                  <Form.Control
                    type="email"
                    placeholder="아이디"
                    className="input-field"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                  />
                </InputGroup>

                <InputGroup className="mb-3 w-75">
                  <InputGroup.Text>
                    <FontAwesomeIcon icon={faLock} />
                  </InputGroup.Text>
                  <Form.Control
                    type="password"
                    placeholder="비밀번호"
                    className="input-field"
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </InputGroup>

                <Button
                  variant="success"
                  type="submit"
                  className="login-button mb-3"
                >
                  Let's Fit!
                </Button>

                <div className="text-center mt-4">
                  <a href="#register" className="login-link">
                    아직 회원이 아니신가요?{" "}
                    <strong style={{ color: "black" }}>회원가입</strong>
                  </a>
                  <br />
                  <a href="#forgotpassword" className="login-link">
                    비밀번호를 잊으셨나요?
                  </a>
                </div>

                <Button variant="warning" className="kakao-login-button mb-3">
                  <FontAwesomeIcon icon={faCommentDots} className="me-2" />{" "}
                  {/* 카카오 아이콘 */}
                  카카오로 간편 가입
                </Button>
              </Form>
            </Col>
          </Row>
        </Container>
      </div>
    </div>
  );
}

export default Login;
