import React, { useState, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { Navbar, Nav, Container, Image } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell, faHeart } from "@fortawesome/free-solid-svg-icons";
import "../css/Nav.css";

function Navs() {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false); // 가정: 로그인 상태를 true로 설정

  // 페이지 이동 핸들러
  const navigateTo = useCallback((path) => navigate(path), [navigate]);

  // 로그아웃 핸들러
  const handleLogout = useCallback(() => {
    setIsLoggedIn(false);
    navigateTo("/");
  }, [navigateTo]);

  // 프로필 클릭 핸들러
  const handleProfileClick = () => {
    navigateTo("/profile");
  };

  return (
    <Navbar expand="lg" className="bg-body-tertiary">
      <Container style={{ maxWidth: "80%" }}>
        <Navbar.Brand onClick={() => navigateTo("/")}>
          <img src="/img/r1.png" alt="Read Fit 로고" className="navbar-logo" />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto justify-content-center">
            <Nav.Link onClick={() => navigateTo("/")}>홈</Nav.Link>
            <Nav.Link onClick={() => navigateTo("/Board")}>커뮤니티</Nav.Link>
            <Nav.Link onClick={() => navigateTo("/Category")}>
              카테고리
            </Nav.Link>
          </Nav>
          <Nav className="align-items-center">
            {isLoggedIn ? (
              <>
                <div className="d-flex align-items-center">
                  <FontAwesomeIcon
                    icon={faBell}
                    className="mx-2 d-lg-inline d-none"
                    onClick={() => {
                      /* 알림 클릭 핸들러 */
                    }}
                  />
                  <FontAwesomeIcon
                    icon={faHeart}
                    className="mx-2 d-lg-inline d-none"
                    onClick={() => {
                      /* 하트 클릭 핸들러 */
                    }}
                  />
                  <Image
                    src="/img/r1.png"
                    roundedCircle
                    className="navbar-logo mx-2 d-lg-inline d-none"
                    onClick={handleProfileClick}
                  />
                </div>
                <Nav.Link className="d-lg-none" onClick={handleLogout}>
                  마이페이지
                </Nav.Link>
                <Nav.Link className="d-lg-none" onClick={handleLogout}>
                  로그아웃
                </Nav.Link>
              </>
            ) : (
              <>
                <Nav.Link onClick={() => navigateTo("/Login")}>로그인</Nav.Link>
                <Nav.Link onClick={() => navigateTo("/Register")}>
                  회원가입
                </Nav.Link>
              </>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
}

export default Navs;
