import Nav from "../components/Nav";
import React, { Component, useState, useEffect } from "react";
import axios from "axios";
import Paginated from "../components/Paginated";
import { Link } from "react-router-dom";

function Board() {
  const [boards, setBoards] = useState([]);
  useEffect(() => {
    // 서버로부터 멤버 목록을 받아오는 함수
    const fetchBoards = async () => {
      try {
        const response = await axios.get("http://localhost:8081/api/boards");
        setBoards(response.data);
      } catch (error) {
        console.error("Error fetching members:", error);
      }
    };

    // 컴포넌트가 마운트될 때 멤버 목록을 받아오도록 함
    fetchBoards();
  }, []); // 빈 배열을 전달하여 한 번만 호출되도록 함

  return (
    <div className="Board">
      <Nav />
      <div id="content">
        게시판 페이지
        {/* {boards.map((board) => (
        <tr key={board.idx}>
          <td>{board.review_idx}</td>
          <td>{board.mb_id}</td>
          <td>{board.review_title}</td>
          <td>{board.review_region}</td>
          <td>{board.reviewed_at}</td>
        </tr>
      ))} */}
        <Paginated
          data={boards.map((board, cnt) => ({
            idx: boards[cnt].review_idx,
            id: boards[cnt].mb_id,
            title: (
              <Link to={`/boards/${boards[cnt].review_idx}`}>
                {boards[cnt].review_title}
              </Link>
            ),
            time: boards[cnt].reviewed_at,
            region: boards[cnt].review_region,
          }))}
          columns={[
            {
              accessor: "idx",
              Header: "번호",
            },
            {
              accessor: "title",
              Header: "제목",
              width: "70%",
            },
            {
              accessor: "id",
              Header: "작성자",
            },

            {
              accessor: "time",
              Header: "날짜",
            },
            {
              accessor: "region",
              Header: "조회수",
            },
          ]}
        />
        ;
      </div>
    </div>
  );
}

export default Board;
